.apply_way.arrow_apply .apply_step_wrap .apply_step_tit > img {
    margin-top: 10px;
    position: absolute;
    left: -30px;
    right: 0;
    bottom: -40px;
    margin: 0 auto;
}

.global_wrap {
    display: table;
    table-layout: fixed;
    width: 100%;
}

.global_wrap > div {
    display: table-cell;
    vertical-align: top;
    padding: 0 15px;
    position: relative;
}

.global_wrap > div:after {
    content: '';
    width: 25px;
    height: 13px;
    background-image: url("../images/sub/step_arrow.png");
    -webkit-background-size: auto auto;
            background-size: auto;
    background-repeat: no-repeat;
    background-position: left top;
    position: absolute;
    left: -15px;
    top: 70%;
}

.global_wrap > div:first-child:after {
    display: none;
}

.global_wrap > div .top_bol {
    display: table;
    table-layout: fixed;
    width: 100%;
    height: 92px;
    background: #1fa88e;
    border-radius: 92px;
    margin-bottom: 15px;
    position: relative;
}

.global_wrap > div .top_bol:after {
    content: '';
    width: 1px;
    height: 80%;
    background: #ccc;
    position: absolute;
    bottom: -80%;
    left: 40px;
    z-index: -1;
}

.global_wrap > div .top_bol:before {
    content: '';
    width: 1px;
    height: 80%;
    background: #ccc;
    position: absolute;
    bottom: -80%;
    right: 40px;
    z-index: -1;
}

.global_wrap > div .top_bol p {
    display: table-cell;
    vertical-align: middle;
    width: 100%;
}

.global_wrap > div .top_bol p span {
    color: #fff;
    display: block;

    .color_yel {
        color: #ffd759;
    }
}

.global_wrap > div:nth-child(even) .top_bol {
    background: #71728a;
}

.global_wrap > div .bottom_bol {
    display: table;
    table-layout: fixed;
    width: 100%;
    height: 155px;
    position: relative;
    border: 1px solid #ccc;
    background: #fff;
    width: 85%;
    margin: 0 auto;
    padding: 10px 0;
}

.global_wrap > div .bottom_bol p {
    font-size: 1.067rem;
    line-height: 1.5;
}

.global_wrap > div .bottom_bol > div {
    display: table-cell;
    vertical-align: middle;
}

