    padding-bottom: 45px;
    -webkit-box-sizing: border-box;
       -moz-box-sizing: border-box;
            box-sizing: border-box;
}

.global_wrap > div .bottom_bol span {
    display: block;
    width: 100%;
    height: 45px;
    line-height: 45px;
    background: #efefef;
    text-align: center;
    position: absolute;
    bottom: 0;
    left: 0;
}

.global_wrap > div.wide_global {
    width: 425px;
    overflow: hidden;
}

.global_wrap > div.wide_global .bb_left {
    width: 45%;
